.nav {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav-black {
    background-color: #111;
    height: 50px;
}

.nav-logo {
    position: fixed;
    left: 0;
    width: 100px;
    object-fit: contain;
}

.nav-avatar {
    position: fixed;
    padding-top: 10px;
    right: 20px; 
    width: 30px;
    object-fit: contain;
}